/*     NAVBAR     */
#nav {
  position: fixed;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-size: 0.8em;
  line-height: 2.5em;
  letter-spacing: 3px;
  text-align: center;
  list-style: none;
  margin: 0;
  padding: 0;
  z-index: 100;
}

#nav li a {
  text-decoration: none;
  color: rgba(0, 0, 0, 1);
  cursor: pointer;
}

#nav li a:hover {
  color: rgba(0, 0, 0, 0.3);
  cursor: pointer;
}

#nav-top {
  height: 2.5em;
  width: 100vw;
  position: fixed;
  top: 0;
  z-index: 100;
  background-color: rgba(255, 255, 255, 1);
}

#nav-right {
  height: 2.5em;
  width: 100vh;
  position: fixed;
  transform: rotate(90deg);
  transform-origin: left top;
  left: 100%;
  z-index: 100;
  background-color: rgba(255, 255, 255, 1);
}

#nav-bottom {
  height: 2.5em;
  width: 100vw;
  position: fixed;
  bottom: 0;
  z-index: 100;
  background-color: rgba(255, 255, 255, 1);
}

#nav-left {
  height: 2.5em;
  width: 100vh;
  position: fixed;
  transform: rotate(-90deg);
  transform-origin: right top;
  right: 100%;
  z-index: 100;
  background-color: rgba(255, 255, 255, 1);
}







