/*     PROJECT GRID     */

.fade-enter {
  opacity: 0;
  animation-fill-mode: forwards;
}

.fade-enter.fade-enter-active {
  animation-fill-mode: forwards;
  opacity: 1;
  transition: opacity 600ms ease-in;
}

.fade-exit {
  opacity: 1;
  animation-fill-mode: forwards;
}

.fade-exit.fade-exit-active {
  animation-fill-mode: forwards;
  opacity: 0;
  transition: opacity 600ms ease-in;
}

.grid-container {
  margin: 0 2em 0 2em;
  -webkit-transition: all .5s;
  -moz-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
}

