/*     MODAL     */
.modal {
  position: absolute;
  left: 5%;
  width: 90%;
  padding-bottom: 1em;
  margin-bottom: 4em;
  box-sizing: border-box;
  background: #F7F7F7;
  box-shadow: 0 0 1em rgba(0, 0, 0, 0.1);
  filter: blur(0);
  transform: scale(1);
  opacity: 1;
  visibility: visible;
  z-index: 1000;
}

#backdrop {
  background-color: rgba(255, 255, 255, 0.6);
  z-index: 500;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: all 1.3s;
  display: none;
}

.close {
  position: absolute;
  right: 1.5em;
  top: 1.5em;
  width: 1.5em;
  height: 1.5em;
  opacity: 1;
}
.close:hover {
  opacity: 0.3;
}
.close:before, .close:after {
  position: absolute;
  left: 1em;
  content: ' ';
  height: 1em;
  width: 2px;
  background-color: #333;
}
.close:before {
  transform: rotate(45deg);
}
.close:after {
  transform: rotate(-45deg);
}

.close-project {
  position: absolute;
  right: 1.5em;
  top: 2.5em;
  width: 3em;
  height: 1.5em;
  opacity: 1;
}
.close-project:hover {
  opacity: 0.3;
}
.close-project:before, .close-project:after {
  position: absolute;
  left: 1.2em;
  content: ' ';
  height: 1em;
  width: 2px;
  background-color: #000000;
}
.close-project:before {
  transform: rotate(45deg);
}
.close-project:after {
  transform: rotate(-45deg);
}







